<template>
  <a-layout style="height: 100vh;">
    <a-layout-header>
      <a-space style="margin-left: 5%;">
        <template #split><a-divider direction="vertical" /></template>
        <h1 style="font-size: 25px;">课程列表</h1>
        <a-button type="primary" :loading="get_all_loading" @click="reload"
         style="float: right;">
          {{ get_all_loading ? "加载中" : "重新加载" }}
        </a-button>
      </a-space>
    </a-layout-header>

    <a-layout-content>
      <a-table :columns="columns" :data="course" :scroll="{ x:600,y: 1200 }"
      :bordered="{cell:true}"
      style="width: 70%;margin-left: 5%;">
        <template #optional="{ record }">
          <a-button type="outline" @click="detailPage(record.course_id)" >详情</a-button>
        </template>
      </a-table>
    </a-layout-content>
  </a-layout>
</template>
  
<script>
import teacher_api from "@/api/teacher/teacher-api"
import auth_api from "@/api/auth-api";
import { reactive } from "vue";

export default {
  data() {
    return {
      // { title: '序号', dataIndex: 'index', width:100},
      columns: reactive([
        { title: '课程号', dataIndex: 'course_id', width: 150 },
        { title: '课程名', dataIndex: 'course_name'},
        { title: '最大可选人数', dataIndex: 'max_student_number',width: 150 },
        { title: '学分', dataIndex: 'credit', width:100},
        { title: '操作', slotName: 'optional', width: 150,fixed: 'right',},
      ]),
      course: [],
      get_all_loading: false
    }
  },

  mounted() {
    this.get_courses();
  },

  methods: {
    detailPage(course_id){
      this.$router.push({ path: '/teacher/course_detail', query: {course_id} })
      
    },
    reload(){
      this.get_courses();
    },
    set_get_all_btn_loading(is_loading) {
      this.get_all_loading = is_loading
    },

    get_courses() {
      this.set_get_all_btn_loading(true)
      // var tid = sessionStorage.getItem("user_id");
      var tid = auth_api.getcookie("user_id")
      teacher_api.get_courses({ teacher_id: tid })
        .then(resp => {
          this.set_get_all_btn_loading(false)
          if (resp.status_code != 200) {
            this.$message.error(resp.status_text)
            return
          }

          var cdata = []
          var tdata = []
          cdata = resp.data
          for (let index = 0; index < cdata.length; index++) {
            tdata.push({
              index: index+1,
              course_id: cdata[index].course_id,
              course_name: cdata[index].course_name,
              credit: cdata[index].credit,
              max_student_number: cdata[index].max_student_number
            })
          }

          this.course = tdata
          // console.log(tdata)
        })
        .catch(err => {
          this.set_get_all_btn_loading(false)
          this.$message.error("加载失败")
          console.error(err)
        })
    }
  }
}
</script>
  